x solid #38cc8c;background:#38cc8c;color:#fff;text-transform:uppercase;font-size:1.2rem;font-weight:600;letter-spacing:0.1rem;box-shadow:#43a87c 1px 4px 0px;cursor:pointer;transition:all 0.5s}.signup-form .signup-btn:hover{background:#86d0b0}.signup-form .terms{margin-top:1rem;color:#b9b6d3}.signup-form .terms .terms-link{color:#ff7a7a;text-decoration:none;font-weight:600;transition:all 0.5s}.signup-form .terms .terms-link:hover{color:#3e3c49}@media (min-width: 980px){.continer{grid-template-columns:50% 1fr;grid-template-areas:" . priceInfo" "introContent signupForm";grid-row-gap:2.2rem;grid-column-gap:2rem;-webkit-align-items:center;align-items:center;background-image:url(/FM-intro-component-with-signup-form/static/media/bg-intro-desktop.059183ee.png),linear-gradient(#ff7a7a, #ff7a7a);padding:5rem 3rem;height:100%;min-height:100vh;text-align:left}.intro-content{padding:0rem 0rem 0rem 5rem;-webkit-align-self:start;align-self:start;justify-self:right;max-width:52rem;margin-top:6rem}.intro-content .title{font-size:4.6rem;line-height:4.2rem}.intro-content .discription{margin-top:2.5rem;font-size:1.5rem}.price-info{-webkit-align-self:end;align-self:end;text-align:center;max-width:50rem;min-height:4.6rem;-webkit-flex-direction:row;flex-direction:row;padding:2rem 0rem;font-size:1.3rem}.signup-form{-webkit-align-self:start;align-self:start;padding:3.5rem;max-width:50rem}.terms{text-align:center}}

